<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go('-1')" />

    <van-search shape="round" show-action placeholder="请输入搜索关键词" clearable v-model="searchInput" @keyup.enter="goSearch(searchInput)" autofocus>
      <template #action>
        <div @click="goSearch(searchInput)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="history.length">
      <div class="title">
        <span>最近搜索</span>
        <van-icon name="delete-o" size="16" @click="clearHistory"/>
      </div>
      <div class="list">
        <div class="list-item" v-for="item in history" :key="item" @click="goSearch(item)">
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearchHistory, setSearchHistory } from '@/utils/storage'

export default {
  name: 'SearchIndex',
  data () {
    return {
      searchInput: '',
      history: getSearchHistory()
    }
  },
  methods: {
    goSearch (key) {
      const index = this.history.indexOf(key)
      console.log(index)
      if (index !== -1) {
        this.history.splice(index, 1)
      }
      this.history.unshift(key)
      setSearchHistory(this.history)
      this.$router.push(`/searchlist?search=${key}`)
    },
    clearHistory () {
      this.history = []
      setSearchHistory([])
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #4988f7;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #4988f7;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
    border-radius: 20px
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
